import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useState } from 'react'
import favicon from '../img/favicon.ico'
import {patient} from '../redux/Detail/detailAction'
import {addNo} from '../redux/Info/noAction'
import {connect} from 'react-redux'
const Form = (props) => {
    const [item,setItem]=useState('');
    const [val,setVal]=useState('');
    const handleChange = (e) =>{
        setItem(e.target.item);
    }
    const handleChangerr = (e) =>{
        setVal(e.target.val);
    }
    const confirm = (event) =>{
        console.log('here in form submit', props)
        event.preventDefault()
        props.dispatch(patient(event.target.fname.value,event.target.lname.value,event.target.dob.value,event.target.contact.value,event.target.email.value))
        console.log("arr-->",props.arr.length)
        props.navigation.next();
    }
    
    return (
        <div>
             {console.log('form')}
        <header>
               <button onClick={()=> { props.navigation.previous() }} className='buttWhite'>
               <i class="bi bi-chevron-compact-left" style={{fontSize:"2rem",color:"#8080806b"}}></i>        
                                    </button>
              <h1>
                  <img src={favicon} style={{height:"1.25rem"}}/>
                  Pharm<span >Access</span>
              </h1>
          </header>
            <h3 className="topic">Patient Info</h3>
            <div class="progress" style={{height:"2px",borderRadius:"0"}}>
              <div class="progress-bar" role="progressbar" 
                    style={{width:"80%",height:"2px",borderRadius:"0"}} 
                    aria-valuenow="80" aria-valuemin="0" aria-valuemax="100">
              </div>  
            </div>
            <br/>
        <h2 className="head" style={{textAlign:"left"}}>Patient#
                    {props.arr.length+1}
        <span style={{color:"red",fontSize:"0.5rem",float:"right"}}>*Required Field</span></h2>
        <div class="container">
        <form class="row g-3 needs-validation"  novalidate onSubmit={confirm}>
            <div class="col-md-4">
                <label for="validationCustom01" class="form-label" style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>First name<span style={{color:"red"}}>*</span></label>
                <input type="text" class="form-control "  id="fname" name="fname" style={{border:"none",borderBottom:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}} required/> 
            </div>
            <div class="col-md-4">
                <label for="validationCustom02" class="form-label" style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>Last name<span style={{color:"red"}}>*</span></label>
                <input type="text" class="form-control "  id="lname" name="lname" style={{border:"none",borderBottom:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}}required/>
            </div>
            <div class="col-md-4">
                <label for="validationCustom03" class="form-label" style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>Date of birth (dd/MM/yyyy)<span style={{color:"red"}}>*</span></label>
                <input type="date" class="form-control "  id="dob" name='dob'  style={{border:"none",borderBottom:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}} required/>
            </div>
            <div class="col-md-4">
                <label for="validationCustom04" class="form-label" style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>Contact<span style={{color:"red"}}>*</span></label>
                <input type="number" class="form-control "  id="contact" name="contact" style={{border:"none",borderBottom:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}} required/>
            </div>
            <div class="col-md-4">
                <label for="validationCustom05" class="form-label" style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>Email<span style={{color:"red"}}>*</span></label>
                <input type="email" class="form-control "  id="email" name="email" style={{border:"none",borderBottom:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}} required/>
            </div>
            {/* <div class="col-md-4">
                <label for="validationCustom07" class="form-label" style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>Type of Appointmnet<span style={{color:"red"}}>*</span></label>
                <select class="form-select "  id="appoint" name="appoint" style={{border:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}} value={item} onChange={handleChange} required>
                    <option selected disabled value="">Choose...</option>
                    <option value="imm">Immunization</option>
                    <option value="medical">Medical Review</option>
                    <option value="otherapp">Any Other Appointmnet</option>                    
                </select>               
            </div>
            {
                item === 'imm' ? (
                <div>
                    <div class="col-md-12" style={{padding:"0px"}}>
                        <label for="validationCustom08" class="form-label" style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>Type of Immunization<span style={{color:"red"}}>*</span></label>
                        <select class="form-select "  id="immunization" name="immunization" style={{border:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}} value={val} onChange={handleChangerr} required>
                            <option selected disabled value="">Choose...</option>
                            <option value="flu" >Flu Vaccine</option>
                            <option value="highflu">High dose Flu Vaccine (Age +65 only)</option>
                            <option value="covid">COVID Vaccine</option>      
                            <option value="others">Other(Prevnar,Shingrex,Havrix,etc)</option>                                  
                        </select>               
                    </div>
                </div>
                ): null
            }
            {
                val === 'flu' ? (
                    <div>
                        <p style={{color:"red",fontSize:"0.75rem",fontFamily: 'Roboto',fontSize:"13px"}}>For faster immunization times due to supply shortages, would you be interested in receving a nasal vaccination (ex.Flumist)?</p>
                        <div class="form-check form-check-inline">
                            <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio1" value="option1"/>
                            <label class="form-check-label" for="inlineRadio1">Yes</label>
                        </div>
                        <div class="form-check form-check-inline">
                            <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio1" value="option1"/>
                            <label class="form-check-label" for="inlineRadio1">No</label>
                        </div>
                    </div>
                ): null 
            }
            {
                val === 'others' ? (
                    <div>
                        <p style={{color:"red",fontSize:"0.75rem",fontFamily: 'Roboto',fontSize:"13px"}}>What immunization injection will you be receiving?</p>
                        <div class="col-md-12" style={{padding:"0px"}}>
                            <input type="email" class="form-control" id="validationCustom09" style={{border:"none",borderBottom:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",fontFamily: 'Roboto',fontSize:"13px"}} required/>
                        </div>
                    </div>
                ): null
            } */}

            <div class="col-12" style={{padding:"0px",paddingLeft:"10px",paddingRight:"10px"}}>
                <label style={{float:"left",fontFamily: 'Roboto',fontSize:"13px"}}>Optional</label>
                 <br/>
                <textarea rows="5" id="comment" placeholder="Add any special instructions for the pharmacy team" style={{border:"1px solid rgb(108 117 125 / 28%)",borderRadius:"0px",width:"100%",fontFamily: 'Roboto',fontSize:"13px",outline:'none'}}></textarea>
            </div>

            <div class="col-12">
                <button class="btn" style={{backgroundColor:"rgb(3,175,182)",color:"white",fontFamily: 'Roboto',fontSize:"13px",width:"16rem"}} 
                    >
                    Confirm
                </button>
            </div>
            
        </form>
        </div>
</div>
    )
}
const mapStateToProps = (state) => ({
    num:state.add.num,
    data: state.add.data,
    arr: state.pDetail.arr,
})
export default connect(mapStateToProps)(Form)
